import item from './templates/item.hbs';
import menu from './menu.json';


const itemsRecall = (src, cardName, price, cardDescr, tagList) => {
  document
    .querySelector('.js-menu')
    .insertAdjacentHTML(
      'beforeend',
      item({ imageUrlHbs: src, cardNameHbs: cardName, priceHbs: price, descrHbs: cardDescr, tagsHbs: tagList}),
    );
};



const getIngredients = (ingArray) => {
  const ingredients = [];
  ingArray.map(item=> ingredients.push(`<li class="tag-list__item">${item}</li>`));
  return ingredients;

}

const menuItems = menu.map(menuItem => {
  const src=menuItem.image;
  const cardName=menuItem.name;
  const price=menuItem.price;
  const cardDescr=menuItem.description;

  const tagList=getIngredients(menuItem.ingredients).join(' ');

  itemsRecall(src, cardName, price, cardDescr, tagList);

});

const imgUrl = {
  imageUrl:
    'https://www.go-cook.ru/wp-content/uploads/2014/11/magribskij-tomatnyj-sup.jpg',
};
