const Theme = {
    LIGHT: 'light-theme',
    DARK: 'dark-theme',
  };


const toggleEl = document.querySelector('#theme-switch-toggle')
const bodyEl = document.querySelector('body');
const lightTheme = 'light-theme';
const darkTheme = 'dark-theme';
let currentStoredTheme = localStorage.getItem('theme');


if (currentStoredTheme==null) {
    localStorage.setItem('theme', lightTheme);
    currentStoredTheme = localStorage.getItem('theme')
}

if (currentStoredTheme===darkTheme) {
    toggleEl.checked='true'
}

bodyEl.classList.add(currentStoredTheme);

const setThemeStorage = (theme) => {
    localStorage.setItem('theme', theme)
}


const onToggleClick = ()=>{
    if(bodyEl.classList.contains('light-theme')){
        bodyEl.classList.add(darkTheme);
        localStorage.setItem('theme', darkTheme);
        currentStoredTheme = localStorage.getItem('theme')
        bodyEl.classList.remove(lightTheme);
    } else {
        bodyEl.classList.add('light-theme')
        localStorage.setItem('theme', lightTheme);
        currentStoredTheme = localStorage.getItem('theme')
        bodyEl.classList.remove('dark-theme')
    }

   }

toggleEl.addEventListener('click', onToggleClick)


